@font-face {
  font-family: "MuseoSansCyrl";
  src: url("../fonts/MuseoSansCyrl-100.woff2") format("woff2"), url("../fonts/MuseoSansCyrl-100.woff") format("woff");
  font-weight: 100;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "MuseoSansCyrl";
  src: url("../fonts/MuseoSansCyrl-300.woff2") format("woff2"), url("../fonts/MuseoSansCyrl-300.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "MuseoSansCyrl";
  src: url("../fonts/MuseoSansCyrl-700.woff2") format("woff2"), url("../fonts/MuseoSansCyrl-700.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.html {
  height: 100%;
  font-family: "MuseoSansCyrl", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16.41px;
  font-weight: 400;
  font-style: normal;
  scroll-behavior: smooth;
  margin: 0 auto;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.body {
  height: 100%;
  background: url("../img/background.png") no-repeat 50% 0/cover;
  display: flex;
  flex-flow: column;
  margin: 0;
}
@media (min-resolution: 2dppx) {
  .body {
    background-image: url(../img/background@2x.png);
  }
}

.main {
  display: grid;
  grid-template-columns: 50% 50%;
  height: 100%;
  max-width: 1170px;
  width: 100%;
  margin: 0 auto;
  padding: 0 50px 0 50px;
}
@media (max-width: 1023.99px) {
  .main {
    padding: 0 20px 0 20px;
  }
}
@media (max-width: 767.99px) {
  .main {
    grid-template-columns: 1fr;
    padding: 0 15px 0 15px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
  padding: 0;
  margin: -1px;
}

a {
  text-decoration: none;
  color: white;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

.header {
  border-bottom: 1px solid #989393;
  width: 100%;
  margin-bottom: 30px;
}

.header-wrapper {
  display: grid;
  grid-template-columns: 35% 65%;
  grid-template-areas: "logo navigation";
  max-width: 1170px;
  width: 100%;
  margin: 0 auto;
  padding: 0 50px 0 50px;
}
@media (max-width: 1023.99px) {
  .header-wrapper {
    grid-template-columns: 45% 55%;
    padding: 0 20px 0 20px;
  }
}
@media (max-width: 767.99px) {
  .header-wrapper {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 0 15px 0 15px;
  }
}

.header__logo-wrapper {
  position: relative;
  border: 1px solid rgba(46, 43, 43, 0.7333333333);
  width: 300px;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 1023.99px) {
  .header__logo-wrapper {
    width: 300px;
    height: 100px;
  }
}
@media (max-width: 767.99px) {
  .header__logo-wrapper {
    position: relative;
    border: 1px solid rgba(46, 43, 43, 0.7333333333);
    width: 300px;
    height: 100px;
    align-self: center;
    margin-top: 10px;
  }
}

.header__corner-top-left {
  position: absolute;
  left: -1px;
  top: -1px;
}

.header__corner-bottom-left {
  position: absolute;
  bottom: -1px;
  left: -1px;
  transform: rotate(-90deg);
}

.header__corner-top-right {
  position: absolute;
  right: -1px;
  top: -1px;
  transform: rotate(90deg);
}

.header__corner-bottom-right {
  position: absolute;
  right: -1px;
  bottom: -1px;
  transform: rotate(180deg);
}

.header__logo-img {
  margin: 0;
  padding: 0;
  width: 100%;
  grid-area: logo;
}

.header__navigation {
  display: flex;
}
.header__list {
  display: flex;
  font-size: 16px;
  line-height: 22px;
  font-weight: 300;
  color: #ffffff;
  list-style-type: none;
  flex-direction: row;
  width: 100%;
  gap: 0 20px;
  align-items: center;
  padding: 0;
}
@media (max-width: 1023.99px) {
  .header__list {
    gap: 0 10px;
    flex-wrap: wrap;
  }
}
@media (max-width: 767.99px) {
  .header__list {
    text-align: center;
    gap: 20px 0;
  }
}

@media (max-width: 767.99px) {
  .header__item {
    width: 50%;
  }
}

.header__link {
  padding: 0 0 15px 0;
}
.header__link:hover {
  transition-duration: 0.3s;
  border-bottom: 1px solid #989393;
  color: #989393;
}

.introdution {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: space-evenly;
}
@media (max-width: 1023.99px) {
  .introdution {
    align-items: center;
  }
}
@media (max-width: 767.99px) {
  .introdution {
    align-items: center;
  }
}

.introdution__purpose-project {
  background: linear-gradient(-90deg, #ff3f04 0%, #ffffff 90%, #ffffff 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 58px;
  line-height: 1.2;
  font-weight: 700;
  text-transform: uppercase;
  color: #ffffff;
  margin: 0;
  padding: 0;
  clip-path: polygon(0 0, 100% 0, 93% 84%, 0% 100%, 0 100%);
}
@media (max-width: 1023.99px) {
  .introdution__purpose-project {
    font-size: 45px;
  }
}
@media (max-width: 767.99px) {
  .introdution__purpose-project {
    font-size: 45px;
  }
}

.about-project {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.about-project__title-for-limited-opportunities {
  visibility: hidden;
  position: absolute;
  top: 0%;
}

.introdution__text-travel {
  background: linear-gradient(-90deg, #ff3f04 0%, #ffffff 90%, #ffffff 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 25px;
  font-weight: 300;
  margin: 0;
  padding: 0;
}
@media (max-width: 1023.99px) {
  .introdution__text-travel {
    font-size: 20px;
    font-weight: 200;
  }
}
@media (max-width: 767.99px) {
  .introdution__text-travel {
    font-size: 20px;
    font-weight: 200;
    text-align: center;
  }
}

.introdution__button-travel {
  position: relative;
  width: 180px;
  height: 35px;
  border-style: solid;
  border-width: 1px;
  border-image-source: linear-gradient(189deg, #263e66 20%, #09213d 40%, #111f2d 61%, #23599c 79%);
  border-image-slice: 1;
  background-origin: border-box;
  background-color: rgba(0, 0, 0, 0.761);
}
.introdution__button-travel:active {
  background-color: #232323;
}
@media (max-width: 1023.99px) {
  .introdution__button-travel {
    width: 180px;
    height: 35px;
  }
}
@media (max-width: 767.99px) {
  .introdution__button-travel {
    width: 170px;
    height: 30px;
  }
}

.introdution__button-travel-text {
  font-size: 14px;
  line-height: 1.2;
  font-weight: 100;
  color: #838383;
}

.introdution__button-corner-left-bottom {
  position: absolute;
  bottom: 0;
  left: 0;
  transform: rotate(-90deg);
}

.introdution__button-corner-right-top {
  position: absolute;
  right: 0;
  top: 0;
  transform: rotate(90deg);
}

.about-project__list {
  display: grid;
  width: 100%;
  grid-template-columns: 1fr 1fr;
  gap: 5%;
  justify-content: center;
  margin: 0;
  padding: 0;
}
@media (max-width: 1023.99px) {
  .about-project__list {
    gap: 3%;
  }
}

.about-project__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 16px;
  line-height: 1.2;
  font-weight: 100;
  color: #f8f7f7;
}
@media (max-width: 1023.99px) {
  .about-project__content {
    font-size: 15px;
  }
}
@media (max-width: 767.99px) {
  .about-project__content {
    font-size: 12px;
  }
}

.about-project__content-number-uppercase {
  font-size: 60px;
  font-weight: 700;
}
@media (max-width: 1023.99px) {
  .about-project__content-number-uppercase {
    font-size: 45px;
  }
}
@media (max-width: 767.99px) {
  .about-project__content-number-uppercase {
    font-size: 40px;
  }
}

.about-project__content-date {
  font-size: 16px;
}

.about-project__item {
  background-color: rgba(255, 255, 255, 0.03);
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  justify-content: center;
  flex-direction: column;
}
.about-project__item:hover {
  background-color: rgba(255, 255, 255, 0.06);
  transform: scale(92.97%);
}